:root {
  --bg-1-color: #303030;
  --bg-2-color: #4e0537;
  --app-1-color: #5e4961ad;
  --app-2-color: #5e1b2b;
  --columns-1-color: #f8f6f8;
  --columns-2-color: #8c0898;
  --task-1-color: #bacbed;
  --text: #fff;
  --main-redius: 10px;
  --blur: 10px;
}

body {
  background-image: linear-gradient(90deg, var(--bg-1-color), var(--bg-2-color));
}

.App {
  text-align: center;
  background: var(--app-1-color);
  border-radius: var(--bg-redius);
  border: 12px double, var(--app-2-color);
  color: var(--text);
  margin: 23px;
  padding: 12px 12px 123px;
}

h2:hover {
  text-shadow: #da1c1c;
  color: #fff;
}

.scrum-board {
  border: 12px double, var(--app-2-color);
  color: var(--text);
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

.add-task-form {
  margin-bottom: 20px;
}

.add-task-form input, .add-task-form select, .add-task-form button {
  border-color: var(--app-2-color);
  margin-right: 10px;
}

.columns {
  border-radius: var(--main-redius), var(--app-2-color);
  justify-content: space-around;
  width: 100%;
  display: flex;
}

.column {
  border: 3px solid var(--app-2-color);
  border-radius: var(--main-redius);
  border-color: var(--columns-1-color);
  width: 30%;
  padding-bottom: 20px;
}

.assignment {
  border: 3px solid var(--task-1-color);
  border-radius: var(--main-redius);
  border-color: var(--task-1-color);
  margin: 10px 0;
  padding: 10px;
}

button {
  margin-top: 10px;
}
